import React, { Component } from 'react';
import PostForm from './PostForm';
import Welcome from './Welcome';
import CvForm from './CvForm';
import SignUp from './SignUp';
import Post from './Post';
import Cv from './Cv';
import Posts from './Posts';
import Dashboard from './Dashboard';
import Confirmation from './Confirmation';
import styled from 'styled-components';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import NavBar from '../components/NavBar';
import Footer from '../components/Footer';

const Container = styled.div`
  max-width: 980px;
  margin: 0 auto;
`;

class App extends Component {
  render() {
    return (
      <div>

        <BrowserRouter>
        <div>
          <NavBar />
          <Container>
            <Switch>
              <Redirect
                from="/home"
                to="/create"
              />
              <Route
                path="/"
                exact
                component={Welcome}
              />
              <Route
                path="/posts"
                exact
                component={Posts}
              />
              <Route
                path="/post/:id"
                exact
                strict
                component={Post}
              />
              <Route
                path="/cv/:id"
                exact
                strict
                component={Cv}
              />
              <Route
                path="/create"
                exact
                component={PostForm}
              />
              <Route
                path="/createcv"
                exact
                component={CvForm}
              />
              <Route
                path="/dashboard"
                exact
                component={Dashboard}
              />
              <Route
                path="/confirmation"
                exact
                component={Confirmation}
              />
              <Route
                path="/signup"
                exact
                component={SignUp}
              />
              <Redirect to="/" />
            </Switch>
          </Container>
        </div>
        </BrowserRouter>
        <Footer />
      </div>
    );
  }
}

export default App;
