import React, { Component } from 'react';
import Cv from '../components/Cv';
import styled from 'styled-components';
import { getCvs } from '../utils/api';
import { withFirebase } from '../components/Firebase';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
  } from "react-router-dom";
import { withSession } from '../components/session';

const INITIAL_STATE = {
  email: '',
  password: '',
  error: null,
};

class Cvs extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  handleSubmit = (e) => {
    const { email, password } = this.state;
    e.preventDefault();

    console.log("Consultando " + email + " " + password);

    this.props.firebase
    .doSignInWithEmailAndPassword(email, password)
    .then(() => {
      this.setState({ ...INITIAL_STATE });
      if(email == 'admin@admin.com'){
        this.props.history.push('/dashboard')
      } else{
        this.props.history.push('/createcv?email='+email)
      }

    })
    .catch(error => {
      console.log(error)
      error.message = "User does not exist"
      this.setState({ error });
    });



  }

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const { email, password, error } = this.state;

    const isInvalid = password === '' || email === '';

    return (
      <div className="container">
          <div class="row">
              <div class="col-sm">
                  <h1 className="text-primary">Welcome</h1>
                  <p>We will help you create your CV, create an account with us with your email address and password to start the process.</p>
              </div>
          </div>

          <div className="dropdown-divider"></div>
          <div className="dropdown-menux">

            <form onSubmit={this.handleSubmit}>
              <div className="form-group">
                <label for="email">Email address</label>
                <input type="email" value={email} name="email" onChange={this.onChange}
                className="form-control" id="email"
                placeholder="email@example.com" />
              </div>
              <div className="form-group">
                <label for="password">Password</label>
                <input type="password" className="form-control" id="password" name="password"
                placeholder="Password" value={password} onChange={this.onChange} />
              </div>
              <div className="form-check">
                <input type="checkbox" className="form-check-input" id="dropdownCheck" />
                <label className="form-check-label" for="dropdownCheck">
                  Remember me
                </label>
              </div>
              <br />
              <button type="submit" className="btn btn-primary">Sign in</button>
              <br />
              {error && <p className="text-danger">{error.message}</p>}
            </form>

            <Link to="/SignUp">Sign up</Link>
            <div className="dropdown-divider"></div>

          </div>

          <br /><br /><br /><br />

      </div>
    );
  }
}



export default withFirebase(Cvs);
