import axios from 'axios';

const BASE_URL = 'https://jsonplaceholder.typicode.com/posts'
const CVREVIEW_URL = 'cvs.json'
const CV_URL = '../cv.json'

export function getPosts() {
  return axios.get(BASE_URL);
}

export function getCvs() {
  return axios.get(CVREVIEW_URL);
}

export function getPost(id) {
  return axios({
    method: 'get',
    url: BASE_URL,
    params: { id },
  })
}

export function getCv(id) {
  console.log("invocando metodo getCv")
  return axios({
    method: 'get',
    url: CV_URL,
    params: { id },
  })
}

export function postPost({ title, body }) {
  return axios({
    method: 'post',
    url: BASE_URL,
    data: {
      title,
      body,
    }
  })
}
